html, body {
  min-height:100%;
  padding:0; 
  margin:0;
  overflow-x: hidden; 
}
a {
    color:#000000;
    text-decoration: none;
}
body div.home {
  height:100vh;
  width:100vw;
  font-size: 22px;
  font-weight: 700;
  letter-spacing: .1rem;
}
.home {
    background-color:#ffffff;
    padding-left: 2vw;
}

.titleBar {
    position:relative;
    top: 11px;
    left: -1vw;
    font-size: 22px;
}

.titleBar, .titleBar a {
    text-align: center;
    color:#3f3f3f;
}

div svg {
  width: 24vw;
  height: 100%;
  overflow: visible;
}

div.dispTitleSketch, div.dispTitleWireframe,div.dispTitlePrototype, div.dispTitleDevelop  {
    width: 24%;
    display:inline-block;
    position:relative;
    top: -52%;
    text-align: center;
}

div svg circle {
  cx: 50%;
  cy: 50%;
  r: 25%;
  stroke: none;
  transition: r .5s;
}

div svg.nav {
  width: 2rem;
  height: 2rem;
  overflow: visible;
  position:relative;
  top:-1.80rem;
  left:1.4rem;
}

div svg circle.nav {
  cx: 50%;
  cy: 50%;
  r: 25%;
  stroke: none;
  transition: r .5s;
}

a:focus svg circle {
    r: 100%;
}

a.link-sketch svg circle {
    fill: #5ADF8E;
}

a.link-wireframe svg circle {
  fill: #F881B9;
}

a.link-prototype svg circle {
  fill: #49DCD9;
}

a.link-develop svg circle {
  fill: #F9FB42;
}

a svg circle:hover {
    opacity:.8;
    transition: r 1s;
    r: 40%;
}

.backArrow {
  margin-bottom:2rem;
  display:inline;
  font-size:1.5rem;
  line-height:1.5rem;
  transition: font-size .5s;
}

.backArrow:hover {
  font-size: 2rem;
}

@media only screen and (max-width: 600px) {
  .tinyNav {
    display:inline;
    position:relative;
    top:2.3rem;
    left:-1rem;
  }  
  .home svg {
    display:block;
    height: unset;
    margin-left: auto;
    margin-right:auto;
    margin-top:4rem;
    margin-bottom:4rem;
  }
  div.dispTitleSketch, div.dispTitleWireframe,div.dispTitlePrototype, div.dispTitleDevelop {
      width:100%;
      display:block;
      position:absolute;
    }
    div.dispTitleSketch {
      top:10.4rem;
    }
    div.dispTitleWireframe {
      top:22.2rem;
    }
    div.dispTitlePrototype {
      top:34rem;
    }
    div.dispTitleDevelop {
      top: 45.8rem
    }
    .home svg circle {
      r: 50%;
    }
    .home svg circle:hover {
      r: 60%;
    }
    .wireframe-container .inner50 img {
      border-radius:0px;
    }
  }


@media only screen and (max-width: 1200px) {
    div.dispTitleSketch, div.dispTitleWireframe,div.dispTitlePrototype, div.dispTitleDevelop {
      font-size: 0.7rem;
    }
  }


@media only screen and (max-width: 1200px) {
    div.dispTitleSketch, div.dispTitleWireframe,div.dispTitlePrototype, div.dispTitleDevelop {
      font-size: 0.7rem;
    }
  }

  @media only screen and (min-height: 1600px) {
    div.dispTitleSketch, div.dispTitleWireframe,div.dispTitlePrototype, div.dispTitleDevelop {
      top:-51%;
    }
  }