.sketch-container {
    background-color: #5ADF8E;
    padding:1rem 5rem 5rem 5rem;
    min-height:100vh;
}
.sketch{
    font-size: 1.4rem;
    font-weight: 700;
    letter-spacing: .1rem;
    margin-bottom: 3rem;
}

.bodyText {
    letter-spacing: .1rem;
    line-height:1.5rem;
    margin-bottom:1.5rem;
}

.screenshots {
    display:flex;
}

.screenshots .sketchShot {
    flex-flow: row;
    margin-bottom:3rem;
}

.sketchShot .column {
    flex: 50%;
}

.screenshots .sketchShot img {
    border-radius: 5rem;
    padding-right:2rem;
    height:60vh;
    width:44.5vw;
}


.sketchContainer, .sketchHeader {
    width:100%;
}
.sketchContainer .inner, .sketchHeader .inner {
    width:50%;
    float:left;
}
.sketchContainer .inner {
    text-align: center;
}
.sketchHeader .inner {
    position:relative;
    left:2.5rem;
    font-size: 1.3rem;
    line-height:4rem;
    font-weight:700;
    letter-spacing: .1rem;
}

.sketchHeader .inner img {
    width:50px;
    height:50px;
    float:right;
    margin-right:5rem;
    position:relative;
    top:.4rem;
}

.innerBorder {
    border:2px solid black;
    border-radius: 3rem;
}

.innerBorder.left{
    width: 98%;
}

.innerBorder img {
    border-radius: 3rem;
    margin-top:5rem;
    margin-bottom: 5rem;
    max-width:98%;
}

.innerBorder.right img {
    max-width:96.5%;
}


.clear {
    clear:both;
}

.toolsContainer {
    border-radius: 2rem;
    padding: 2rem;
    letter-spacing: .1rem;
    background-color:rgba(255,255,255,.5);
    margin-top:2%;
}

.toolsContainer .header {
    font-weight:600;
}


p {
    padding-top:4%
}

.scaleContainer .inner50 {
    border-radius: 3rem;
    padding: 3%;
    display:inline-block;
    text-align: center;
    border:2px solid black;
    width:auto;
}

.inner50.right {
    margin-left:1.5rem;
    height:100%;
}

.inner50.left {
    margin-right:1.5rem;
}

.inner50 img {
    max-width: 100%;
    max-height:100%;
    border-radius: 3rem;
}