.prototype-container {
    background-color: #49DCD9;
    padding:1rem 5rem 5rem 5rem;
    height:100vh;
}
.prototype{
    font-size: 1.4rem;
    font-weight: 700;
  letter-spacing: .1rem;
  margin-bottom: 3rem;
}


.scaleContainer {
    letter-spacing: .1rem;
    text-align: center;
    width:100%;
}

p {
    padding-top:4%
}

.scaleContainer .inner50 {
    border-radius: 3rem;
    padding: 3%;
    display:inline-block;
    text-align: center;
    border:2px solid black;
    margin-bottom:3rem;
    height:auto;
    width:42vw;
}

.inner50.right {
    margin-left:1.5rem;
}

.inner50.left {
    margin-right:1.5rem;
}

.inner50 img {
    max-width: 100%;
    max-height:100%;
}


iframe {
    border-radius: 2.9rem;
}




@media only screen and (max-width: 600px) {
    .prototype {
        padding:2rem;
    }
}