.wireframe-container {
    background-color: #F881B9;
    padding:1rem 5rem 5rem 5rem;
    min-height:100vh;
}
.wireframe{
    font-size: 1.4rem;
    font-weight: 700;
  letter-spacing: .1rem;
  margin-bottom: 3rem;
}

.scaleContainer {
    letter-spacing: .1rem;
    text-align: center;
    width:100%;
}

p {
    padding-top:4%
}

.scaleContainer .inner50 {
    border-radius: 3rem;
    padding: 3%;
    display:inline-block;
    text-align: center;
    border:2px solid black;
    margin-bottom:3rem;
    width:auto;
}

.inner50.right {
    margin-left:1.5rem;
}

.inner50.left {
    margin-right:1.5rem;
}

.inner50 img {
    max-width: 100%;
    max-height:100%;
}


@media only screen and (max-width: 2255px) {
    .inner50.right {
        margin-left:0%;
    }
    .inner50.left {
        margin-right:0%;
    }
    .inner50 {
        width:94%;
    }
}


@media only screen and (max-width: 600px) {
    .wireframe {
        padding:2rem;
    }
}